<template>
  <div id="app">
    <img src="./assets/logo.png">
    <h1>{{ msg }}</h1>
    <select v-model="selectedCountry">
      <option v-for="country in countries" :value="country.value">{{ country.name}}</option>
    </select>
    <spinner v-show="loading"></spinner>
    <ul>
      <artist v-for="artist in artists" v-bind:artist="artist" v-bind:key="artist.mbid"></artist>
    </ul>
  </div>
</template>

<script>
import Artist from "./components/Artist";
import Spinner from "./components/Spinner";
import getArtist from "./api";

export default {
  name: "app",
  data() {
    return {
      artists: [],
      countries: [
        { name: "Argentina", value: "argentina" },
        { name: "Colombia", value: "colombia" },
        { name: "Espaa", value: "spain" }
      ],
      selectedCountry: "colombia",
      loading: true,
      msg: "Platzi Music"
    };
  },
  components: {
    Artist: Artist,
    Spinner: Spinner
  },
  methods: {
    refreshArtist() {
      const self = this;
      this.loading = true;
      this.artists = [];
      getArtist(this.selectedCountry).then(function(artist) {
        self.loading = false;
        self.artists = artist;
      });
    }
  },
  mounted: function() {
    this.refreshArtist();
  },
  watch: {
    selectedCountry: function() {
      this.refreshArtist();
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



